.notificationContainer {
  direction: ltr;
}
.notificationContainer > img {
  width: 60px;
  height: 60px;
  border-radius: 15px;
  margin-right: 10px;
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.03);
}
.notification div:nth-child(2) {
  font-size: 12px;
  line-height: 1;
  color: #6e6e6e;
}
